
// SECTION 2
const robot_text = "Une Intelligence Artificielle qui analyse et trade sur le marché des crypto-monnaies 24h/24h !";
document.getElementById("robot__text").textContent = robot_text;

const money_text = "Un moyen pour faire fructifier votre argent à hauteur d’environ 10% par mois !";
document.getElementById("money__text").textContent = money_text;

const plus_text = "Un projet qui soutient <À définir> !";
document.getElementById("plus__text").innerHTML = plus_text;

// AUTO SCROLL

	// Récupération des éléments intéressants dans des variables
	const section_1 = document.getElementById("main-block__section-1");
	const section_2 = document.getElementById("main-block__section-2");
	const section_3 = document.getElementById("main-block__section-3");
	
	let can_scroll = true;
	window.addEventListener('scroll', () => {
		// Récupère la position en Y des éléments
		const section_1_y = section_1.getBoundingClientRect()["y"];
		const section_2_y = section_2.getBoundingClientRect()["y"];
		const section_3_y = section_3.getBoundingClientRect()["y"];

		// Position de la fenêtre
		let scroll_y = window.scrollY;
		
		if (can_scroll) {
				// Scroll down sur première page
				// S'active si fenêtre sur l'intervalle -5% à -30% du bloc section_1
				if ((section_1_y < -(section_2_y - section_1_y)*0.05) && (section_1_y > -(section_2_y - section_1_y)*0.3)) {
					can_scroll = false;
					section_2.scrollIntoView();

					setTimeout(() => { // Réactive la possibilité de scroll après 500ms
						can_scroll =true;
					}, 500);
				}

				// Scroll up sur deuxième page
				if ((section_2_y > (section_2_y - section_1_y)*0.05) && (section_2_y < (section_2_y - section_1_y)*0.3)) {
					can_scroll = false;
					section_1.scrollIntoView();

					setTimeout(() => {
						can_scroll =true;
					}, 500);
				};

				// Scroll down sur deuxième page
				if ((section_2_y < -(section_3_y - section_2_y)*0.05) && (section_2_y > -(section_3_y - section_2_y)*0.3)) {
					can_scroll = false;
					section_3.scrollIntoView();

					setTimeout(() => {
						can_scroll =true;
					}, 500);
				};

				// Scroll up sur troisième page
				if ((section_3_y > (section_3_y - section_2_y)*0.05) && (section_3_y < (section_3_y - section_2_y)*0.3)) {
					can_scroll = false;
					section_2.scrollIntoView();

					setTimeout(() => {
						can_scroll =true;
					}, 500);
				};
		};
	});

// END - AUTO SCROLL